.App {
	font-size: 46px;
	height: 100vh;
	width: 100vw;
	background: #282828;

	display: grid;
	grid-template-columns: repeat(10, 1fr);
	grid-column-gap: 2px;
	grid-row-gap: 2px;
	grid-template-rows: repeat(15, 1fr);
}
.header {
	padding-top: 1.5vh;
	color: white;
	background-color: #1b1c1d;
	grid-column: 1/11;
	grid-row: 1/2;
	margin-right: 10px;
}

.display {
	background-color: #1b1c1d;
	grid-column: 1/11;
	grid-row: 2/15;
	display: grid;
	grid-template-columns: repeat(10, 1fr);
	grid-template-rows: repeat(15, 1fr);
	grid-gap: 5px;
}

.footer {
	padding-top: 1.5vh;
	font-size: 1rem;
	color: white;
	background-color: #1b1c1d;
	grid-column: 1/11;
	grid-row: 15/16;
}
.counter {
	color: white;
}
.main {
	grid-column: 3/11;
	grid-row: 1/9;
	background-color: #1b1c1d;
	margin-right: 30px;

	display: grid;
	grid-template-columns: repeat(10, 1fr);
	grid-template-rows: repeat(10, 5vh);

	/* grid-template-rows: repeat(15, 1fr); */
}
.response {
	background-color: #282828;
	font-size: 2vh;
	margin-right: 30px;
	grid-column: 3/11;
	grid-row: 9/16;

	color: white;
	overflow-x: scroll;
	overflow-y: scroll;
	scrollbar-width: 10px;
	scrollbar-color: #1b1c1d #282828;
}

.response_tests {
	background-color: #282828;
	font-size: 2vh;

	color: white;
	overflow-x: scroll;
	overflow-y: scroll;
	scrollbar-width: 10px;
	scrollbar-color: #1b1c1d #282828;
}

.sidebar {
	background-color: #282828;
	font-size: 2vh;
	grid-column: 1/3;
	margin-top: 5px;
	grid-row: 1/16;
	overflow-x: scroll;
	overflow-y: scroll;
	scrollbar-width: thin;
	scrollbar-color: #1b1c1d #282828;
}
.example {
	color: white;
	float: left;
}
.exampleMethod {
	float: right;
}
.instructions {
	font-size: 1rem;
	padding-left: 2vw;
}
.history {
	background-color: #282828;
	color: white;
}
.label {
	padding-top: 10px;
	color: white;
}
.iconPlay {
	float: right;
	color: black;
}
.collections {
	background-color: #282828;
}
.collectionName {
	float: left;
}
.inputCollection {
	color: white !important;
}

.queryParams {
	background-color: #282828;
	font-size: 2vh;
	grid-column: 1/11;
	border: grey;
	color: #fff;
	display: grid;
	grid-template-columns: repeat(9, 1fr);
}
.tabsComponent {
	grid-column: 1/11;
}
.queryParamsHeading {
	grid-column: 1/2;
	padding-top: 1vh;
}
.keyValue {
	grid-column: 1/10;
	padding-top: 1vh;
	background-color: #282828;
}
.title {
	background-color: #282828;
	font-size: 2vh;
	padding-top: 1vh;
	padding-left: 1vw;

	grid-column: 1/11;
	grid-row: 1/2;
	border: 1px solid grey;
}
.name {
	font-size: 0.5em;
	padding-top: 1vh;
	padding-left: 1.5vw;
	float: left;
	color: #fff;
}
.comments {
	float: right;
	font-size: 0.5em;
	margin-top: 1vh;
	margin-right: 1.5vw;
	color: #fff;
	border: 5px;
	border-color: grey;
}
.urlComponent {
	background-color: #282828;
	grid-column: 1/11;
	grid-row: 2/10;

	display: grid;
	grid-template-columns: repeat(10, 1fr);
	grid-template-rows: repeat(6, 1fr);
}
.tabsComponent {
	background-color: #282828;
	grid-column: 1/11;
	grid-row: 2/11;

	padding-left: 1.5vw;
	overflow-x: scroll;
	display: grid;
	scrollbar-width: thin;
	scrollbar-color: #1b1c1d #282828;
	grid-template-columns: repeat(10, 1fr);
	grid-template-rows: repeat(6, 1fr);
}
.testsComponent {
	grid-column: 2/8;
	grid-row: 2/4;
	background-color: #282828;
}
.headersTable {
	background-color: #282828;
	grid-column: 1/11;
	grid-row: 1/9;

	grid-template-columns: repeat(10, 1fr);
	grid-template-rows: repeat(6, 1fr);
}
.tabPane {
	background-color: #282828 !important;
	color: white;
}
.body {
	display: grid;
	grid-template-columns: repeat(10, 4vw);
	grid-template-rows: repeat(6, 3vh);
}
.radioButtons {
	grid-column: 1/6;
	grid-row: 1/6;
}
.another {
	grid-column: 4/16;
	grid-row: 1/7;
}

label {
	color: white;
}
.method {
	background-color: #282828;
	margin-top: 2.5vh;
	margin-left: 2.5vw;
	grid-column: 1/2;
	grid-row: 1/2;
	font-size: 0.85vw;
	color: white;
}
.url {
	grid-column: 2/9;
	grid-row: 1/2;
	margin-top: 4vh;
