.urlInput {
	color: white !important;
}
.inputCollection {
	padding: 10px;
}
.buttonCreate {
	padding: 10px;
}

.tabs {
	color: white !important;
}
.sendButton {
	background-color: #282828;

	margin-top: 2.5vh;
	margin-bottom: 1vh;
	margin-left: 0.5vw;

	font-size: 0.65vw;
	color: #fff;
	font-style: italic;
}

.saveButton {
	background-color: #282828;
	grid-column: 10/11;
